import React, { useState, useEffect, createContext, useContext } from 'react';
import { INITIAL_BELANJA, INITIAL_PENDAPATAN, INITIAL_PEMBIAYAAN } from '../data/initialData';
import SSH_DATA from '../data/data_ssh'; 

const AppContext = createContext();

// --- HELPER FUNCTIONS (Fungsi Hitung) ---
const getOutputTotal = (out) => out.rabs ? out.rabs.reduce((a,b)=>a+b.total,0) : 0;
const getKegiatanTotal = (keg) => keg.outputs ? keg.outputs.reduce((a,b)=>a+getOutputTotal(b),0) : 0;
const getSubTotal = (sub) => sub.kegiatan ? sub.kegiatan.reduce((a,b)=>a+getKegiatanTotal(b),0) : 0;
const getBidangTotal = (bid) => bid.subs ? bid.subs.reduce((a,b)=>a+getSubTotal(b),0) : 0;

export const useAppContext = () => useContext(AppContext);

export const AppProvider = ({ children }) => {
    // 1. User & Login
    const [user, setUser] = useState(() => {
        const saved = localStorage.getItem('rapbdes_user');
        return saved ? JSON.parse(saved) : null;
    });
    
    const [showLoginModal, setShowLoginModal] = useState(false);
    
    // 2. Load Data
    const [dataBelanja, setDataBelanja] = useState(() => {
        const saved = localStorage.getItem('rapbdes_belanja');
        return saved ? JSON.parse(saved) : INITIAL_BELANJA;
    });

    const [dataPendapatan, setDataPendapatan] = useState(() => {
        const saved = localStorage.getItem('rapbdes_pendapatan');
        return saved ? JSON.parse(saved) : INITIAL_PENDAPATAN;
    });

    const [dataPembiayaan, setDataPembiayaan] = useState(() => {
        const saved = localStorage.getItem('rapbdes_pembiayaan');
        return saved ? JSON.parse(saved) : INITIAL_PEMBIAYAAN;
    });
    
    const [dataKey, setDataKey] = useState(Date.now()); 
    const [dataSSH] = useState(SSH_DATA || []);

    // 3. Auto-Save
    useEffect(() => {
        localStorage.setItem('rapbdes_belanja', JSON.stringify(dataBelanja));
        localStorage.setItem('rapbdes_pendapatan', JSON.stringify(dataPendapatan));
        localStorage.setItem('rapbdes_pembiayaan', JSON.stringify(dataPembiayaan));
    }, [dataBelanja, dataPendapatan, dataPembiayaan]);

    // 4. Actions
    const login = (u, p) => {
        if(u === 'Admin' && p === '123') {
            const data = { username: 'Admin' };
            setUser(data);
            localStorage.setItem('rapbdes_user', JSON.stringify(data));
            setShowLoginModal(false);
            return true;
        }
        return false;
    };

    const logout = () => {
        setUser(null);
        localStorage.removeItem('rapbdes_user');
    };

    const resetData = () => {
        if(window.confirm("Yakin ingin menghapus semua isian dan kembali ke 0?")) {
            setDataBelanja(INITIAL_BELANJA);
            setDataPendapatan(INITIAL_PENDAPATAN);
            setDataPembiayaan(INITIAL_PEMBIAYAAN);
            localStorage.removeItem('rapbdes_belanja');
            localStorage.removeItem('rapbdes_pendapatan');
            localStorage.removeItem('rapbdes_pembiayaan');
            window.location.reload();
        }
    };

    const getTotalBelanja = () => dataBelanja.reduce((a,b)=>a+getBidangTotal(b),0);
    const getTotalPendapatan = () => dataPendapatan.reduce((a,b)=>a+b.anggaran,0);
    
    const getTotalPembiayaan = () => {
        const penerimaan = dataPembiayaan.penerimaan.reduce((a,b)=>a+b.anggaran,0);
        const pengeluaran = dataPembiayaan.pengeluaran.reduce((a,b)=>a+b.anggaran,0);
        return { penerimaan, pengeluaran, neto: penerimaan - pengeluaran };
    };

    const updateRAB = (bidId, subId, kegId, outId, newRabs) => {
        const newData = [...dataBelanja];
        const b = newData.find(x=>x.id===bidId);
        if(b) {
            const s = b.subs.find(x=>x.id===subId);
            if(s) {
                const k = s.kegiatan.find(x=>x.id===kegId);
                if(k) {
                    const o = k.outputs.find(x=>x.id===outId);
                    if(o) { 
                        o.rabs = newRabs; 
                        setDataBelanja(newData); 
                        setDataKey(Date.now()); 
                    }
                }
            }
        }
    };

    // Fungsi Tambah Output Baru
    const addOutput = (bidId, subId, kegId, uraian) => {
        const newData = [...dataBelanja];
        const b = newData.find(x => x.id === bidId);
        if (b) {
            const s = b.subs.find(x => x.id === subId);
            if (s) {
                const k = s.kegiatan.find(x => x.id === kegId);
                if (k) {
                    k.outputs.push({
                        id: Date.now().toString(),
                        uraian: uraian,
                        sat: 'Paket',
                        rabs: []
                    });
                    setDataBelanja(newData);
                    setDataKey(Date.now()); 
                }
            }
        }
    };

    return (
        <AppContext.Provider value={{ 
            user, showLoginModal, setShowLoginModal, login, logout, resetData,
            dataBelanja, setDataBelanja,
            dataPendapatan, setDataPendapatan,
            dataPembiayaan, setDataPembiayaan,
            dataSSH, 
            
            // FUNGSI HITUNG (Sudah dicek tidak ada yang dobel)
            getTotalBelanja, getTotalPendapatan, getTotalPembiayaan,
            getSubTotal, getKegiatanTotal, getOutputTotal, getBidangTotal,
            
            updateRAB,
            addOutput,
            dataKey
        }}>
            {children}
        </AppContext.Provider>
    );
};